import React, { FC, useEffect, useState } from 'react';
import { useForm } from 'react-hook-form';
import { useSelector } from 'react-redux';
import { RouteComponentProps } from 'react-router';
import { Col, CustomInput, FormGroup, Row } from 'reactstrap';
import { WindowComponent, WindowComponentProps } from 'summer';
import { hasEmpresa } from '../../../../../dependencies/empresaDependency';
import { hasEmpresaMFP } from '../../../../../dependencies/empresaMFPDependency';
import { getDefaultReportBasicValues } from '../../../../../models/dtos/ger/reportBasicValues';
import { getDefaultReportViewOptions, ReportViewOptions } from '../../../../../models/dtos/ger/reportViewOptions';
import { Reducers } from '../../../../../store/ducks';
import { GlobalState } from '../../../../../store/ducks/global.duck';
import VinculoIS from '../../../../components/mfp/vinculoIS/VinculoIS';
import ReportForm from '../../../../components/report/ReportForm';
import { CLASS_KEY, RegistroEmpregadoRV, REPORT_EVENT, REPORT_TITLE } from './registroEmpregadoRV';

const DEFAULT_VALUES: RegistroEmpregadoRV = {
  ...getDefaultReportBasicValues(REPORT_TITLE),
  gerarCabecalhoPadraoAdministradora: true,
  vinculos: [],
};

const DEFAULT_OPTIONS: ReportViewOptions = {
  ...getDefaultReportViewOptions(REPORT_EVENT),
  exibirConfiguracaoDadosEmpresa: true,
};

const RegistroEmpregadoReportView: FC<WindowComponentProps & RouteComponentProps> = props => {
  const globalReducer = useSelector<Reducers, GlobalState>(state => state.globalReducer);
  const { handleSubmit, ...formProps } = useForm<RegistroEmpregadoRV>({ defaultValues: DEFAULT_VALUES, mode: 'onBlur' });
  const [isFormBlank, setIsFormBlank] = useState(true);

  useEffect(
    () => {
      if (formProps.getValues().isFormularioBranco) {
        setIsFormBlank(false);
      } else {
        setIsFormBlank(true);
      }
    }, // eslint-disable-next-line react-hooks/exhaustive-deps
    [formProps.getValues().isFormularioBranco]
  );

  return (
    <WindowComponent
      title="Registro de Empregados"
      useGlobalParameter={true}
      globalParameter={globalReducer.globalParameter}
      dependencies={[() => hasEmpresa(globalReducer.globalParameter), () => hasEmpresaMFP(globalReducer.globalParameter)]}
      wikiId={3213}
    >
      <ReportForm
        {...props}
        handleSubmit={handleSubmit}
        formProps={formProps}
        localStorageKey={CLASS_KEY}
        routeProps={props}
        reportBasicValues={DEFAULT_VALUES}
        reportViewOptions={DEFAULT_OPTIONS}
      >
        <FormGroup tag={Row}>
          <Col>
            <CustomInput
              id="checkIsFormularioBranco"
              label="Imprimir Formulário em Branco"
              name="isFormularioBranco"
              innerRef={formProps.register}
              title="Imprimir Formulário em Branco para preenchimento manual dos Dados Pessoais, Cargos, Salários, Férias, Afastamentos e Contribuições"
              type="switch"
            />
          </Col>
        </FormGroup>
        {isFormBlank ? (
          <>
            <FormGroup tag={Row}>
              <Col>
                <CustomInput
                  id="checkIsExibirAnotacoes"
                  label="Imprimir Linhas em Branco Para Anotações Futuras"
                  name="isExibirAnotacoes"
                  innerRef={formProps.register}
                  title="Imprimir Linhas em Branco Para Anotação em Cargos, Salários, Férias, Afastamentos e Contribuições"
                  type="switch"
                />
              </Col>
            </FormGroup>

            <VinculoIS
              id="vinculos"
              isPrependLabel={true}
              inputSearchProps={{ selectProps: { isMulti: true } }}
              formProps={formProps}
              label="Vínculo(s)"
              placeholder="Informe os vínculos"
            />
          </>
        ) : null}
      </ReportForm>
    </WindowComponent>
  );
};

export default RegistroEmpregadoReportView;
